
import React, { Component } from 'react';
import { Layout } from 'antd';
import { withRouter } from 'react-router-dom';
import logo from '../style/imgs/logo.png';
import { Menu, Icon } from 'antd';
import { Link } from 'react-router-dom';
const { Sider } = Layout;
const SubMenu = Menu.SubMenu;

class SiderCustom extends Component {
    state = {
        collapsed: false,
        mode: 'inline',
        openKey: '',
        selectedKey: '',
        firstHide: true,        // openMenu
        menuArr: [
            {   key: '/app/realtime', title: '', icon: 'clock-circle-o',
                sub: [
                    { key: '/app/realtime/survey', title: ''},
                    { key: '/app/realtime/order', title: ''}
                ]
            },
            {
                key: '/app/operatingdaily', title: '', icon: 'schedule',
                sub: [
                    { key: '/app/operatingdaily/incomeCost', title: ''},
                    { key: '/app/operatingdaily/distribution', title: ''},
                    { key: '/app/operatingdaily/portrait', title: ''},
                    { key: '/app/operatingdaily/bargainingAnalysis', title: ''},
                    { key: '/app/operatingdaily/capacityAnalysis', title: ''},
                    { key: '/app/operatingdaily/userStatistics', title: ''},
                    { key: '/app/operatingdaily/piesAnalysis', title: ''},
                    { key: '/app/operatingdaily/serviceQualityOfDrivers', title: ''}
                ]
            },
            {
                key: '/app/ranking', title: '', icon: 'flag',
                sub: [
                    { key: '/app/ranking/rank_order', title: '',
                        sub: [
                            { key: '/app/ranking/rank_order/complete_count', title: ''},
                            { key: '/app/ranking/rank_order/complete_amount', title: ''},
                            { key: '/app/ranking/rank_order/average_amount', title: ''},
                            { key: '/app/ranking/rank_order/average_time', title: ''},
                            { key: '/app/ranking/rank_order/average_distance', title: ''},
                            { key: '/app/ranking/rank_order/complete_rate', title: 'C to R'},
                            { key: '/app/ranking/rank_order/create_total', title: ''},
                            { key: '/app/ranking/rank_order/dispatch_success_rate', title: ''},
                            { key: '/app/ranking/rank_order/billing_difference', title: ''},
                            { key: '/app/ranking/rank_order/service_cost', title: ''},
                            { key: '/app/ranking/rank_order/no_car_can_dispatch', title: ''}
                        ]
                    },
                    { key: '/app/ranking/rank_driver', title: '',
                        sub: [
                            { key: '/app/ranking/rank_driver/active_total', title: ''},
                            { key: '/app/ranking/rank_driver/average_orders', title: ''},
                            { key: '/app/ranking/rank_driver/registered_total', title: ''},
                            { key: '/app/ranking/rank_driver/activation_total', title: ''},
                            { key: '/app/ranking/rank_driver/negative_rate', title: ''}
                        ]
                    },
                    { key: '/app/ranking/rank_user', title: '',
                        sub: [
                            { key: '/app/ranking/rank_user/active_total', title: ''},
                            { key: '/app/ranking/rank_user/average_total', title: ''},
                            { key: '/app/ranking/rank_user/registered_total', title: ''},
                            { key: '/app/ranking/rank_user/activation_total', title: ''},
                            { key: '/app/ranking/rank_user/recharge_amount', title: ''},
                            { key: '/app/ranking/rank_user/place_order_user_total', title: ''}
                        ]
                    }
                ]
            }
        ]
    };
    componentDidMount() {
        this.setMenuOpen(this.props);
    }
    componentWillReceiveProps(nextProps) {
        console.log(nextProps);
        this.onCollapse(nextProps.collapsed);
        this.setMenuOpen(nextProps)
    }
    setMenuOpen = props => {
        const { pathname } = props.location;
        this.setState({
            openKey: pathname.substr(0, pathname.lastIndexOf('/')),
            selectedKey: pathname
        });
    };
    onCollapse = (collapsed) => {
        this.setState({
            collapsed,
            firstHide: collapsed,
            mode: collapsed ? 'vertical' : 'inline',
        });
    };
    menuClick = e => {
        this.setState({
            selectedKey: e.key
        });
        const { popoverHide } = this.props;     // 
        popoverHide && popoverHide();
    };
    openMenu = v => {
        this.setState({
            openKey: v[v.length - 1],
            firstHide: false,
        })
    };
    formSubmenusChild(obj){
        let cHtml=<div></div>;
        let childArray=obj.sub;
        if("undefined"!=typeof(childArray)&&childArray.length>0) {
            cHtml = childArray.map((item, i) => {
                return this.formSubmenusChild(item);
            });
            if(obj.icon){
                return <SubMenu key={obj.key} title={<span><Icon type={obj.icon}></Icon><span className="nav-text">{obj.title}</span></span>}>{cHtml}</SubMenu>
            }else {
                return <SubMenu key={obj.key} title={obj.title}>{cHtml}</SubMenu>
            }

        }else{
            return <Menu.Item key={obj.key}><Link to={obj.key}>{obj.title}</Link></Menu.Item>
        }

    }

    render() {
        const {openKey, menuArr} = this.state;
        let openKeyKey = ['/app/ranking'],openKeyArr = [];
        if (openKey == '/app/ranking/rank_order' || openKey == '/app/ranking/rank_driver' || openKey == '/app/ranking/rank_user'){
            openKeyKey.push(openKey);
            openKeyArr = openKeyKey;
        }else {
            openKeyArr.push(openKey)
        }
        let columnMenu = menuArr;
        let htmlMenu =columnMenu.map((obj, i)=>{
            if ("undefined"!=typeof(obj.sub)&&obj.sub.length>0) {
                return this.formSubmenusChild(obj);
            } else {
                //routeurl
                return <Menu.Item key={obj.key}>{obj.name}</Menu.Item>
            }
        })
        return (
            <Sider
                trigger={null}
                collapsed={this.props.collapsed}
                style={{ overflowY: 'auto' }}
            >
                <div className="logo">
                    <Link to="/">
                        <img src={logo} alt="logo"/>
                        <span className="logo-title"></span>
                    </Link>
                </div>
                <Menu
                    theme="dark"
                    mode="inline"
                    selectedKeys={[this.state.selectedKey]}
                    openKeys = {this.state.firstHide ? null : openKeyArr}
                    onClick={this.menuClick}
                    onOpenChange={this.openMenu}
                    inlineCollapsed={this.state.collapsed}
                >
                    {htmlMenu}
                </Menu>
                <style>
                    {`
                    #nprogress .spinner{
                        left: ${this.state.collapsed ? '70px' : '206px'};
                        right: 0 !important;
                    }
                    .ant-layout .logo {
                        height: 64px;
                        line-height: 64px;
                        background: #002140;
                        margin: 0;
                        padding-left: 14px;
                    }
                    .logo-title {
                        color: #fff;
                        font-size: 20px;
                        font-weight: 600;
                    }
                    .logo img {
                        display: inline-block;
                        width: 45px;
                        height: 45px;
                    }
                    .logo i img {
                        width: 100%;
                    }
                    `}
                </style>
            </Sider>
        )
    }
}

export default withRouter(SiderCustom);


